export default {
  data() {
    return {
      username: '',
      form: {
        username: '11',
        password: ''
      },
      showDel: false
    }
  },
  methods: {
    onInput(e) {
      let value = e.detail.value
      if (value) {
        this.showDel = true
        return
      }
      this.showDel = false
    },
    clickAction(type) {
      uni.navigateTo({
        url: 'register?type=' + type,
        "animationType": "zoom-fade-out"
      })
    },
    goLogin() {
      uni.switchTab({
        url: '../index/index'
      })
    },
    loginWay(type) {
      uni.getProvider({
        service: 'oauth',
        success: function(res) {
          console.log(res.provider)
          if (~res.provider.indexOf(type)) {
            uni.login({
              provider: type,
              success: function(loginRes) {
                console.log(JSON.stringify(loginRes));
              }
            })
          }
        },
        fail: function(err){
          console.log(err)
        }
      })
    }
  }
}
